/***************************************
Aione Admin Editor
***************************************/
.wp-block {
  width: 98%;
  max-width: 1170px;
}
.wp-block .aione-section{
  border: 2px dashed #cfd8dc;
}
.wp-block.is_selected .aione-section,
.wp-block.is_hovered .aione-section{
  border: 2px dashed #263238;
}

.wp-block .ar{
  border: 2px dashed #ffd29e;
}
.wp-block.is_hovered .ar{
  border: 2px dashed #ff9800;
}

.wp-block .ac{
  border: 2px dashed #80cbc4;
}
/*
.wp-block[data-type="aione-blocks/aione-column"]:after { 
	content: "";
	clear: both;
	display: table;
}
*/

.wp-block.is_hovered .ac{
  border: 2px dashed #26a69a;
}

.wp-block .aione-wrapper{
  border: 1px solid #e8e8e8;
}
.wp-block.is_hovered .aione-wrapper{
  border: 1px solid #787878;
}




.editor--slides-container {
	
}
.editor--slides-container:after{
	content:"";
	clear: both;
	display: table;
}
.editor--slides-container > *{
	float:left;
}
.editor--slides-container.columns-1 > *{ width:100%; }
.editor--slides-container.columns-2 > *{ width:50%; }
.editor--slides-container.columns-3 > *{ width:33%; }
.editor--slides-container.columns-4 > *{ width:25%; }
.editor--slides-container.columns-5 > *{ width:20%; }
.editor--slides-container.columns-6 > *{ width:16.66%; }
.editor--slides-container.columns-7 > *{ width:14.28%; }
.editor--slides-container.columns-8 > *{ width:12.25%; }
.editor--slides-container.columns-9 > *{ width:11.11%; }
.editor--slides-container.columns-10 > *{ width:10%; }
.editor--slides-container.columns-11 > *{ width:9.09%; }
.editor--slides-container.columns-12 > *{ width:8.33%; }
.editor--slides-container.columns-13 > *{ width:7.69%; }
.editor--slides-container.columns-14 > *{ width:7.14%; }
.editor--slides-container.columns-15 > *{ width:6.66%; }
.editor--slides-container.columns-16 > *{ width:5.25%; }
